@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(http://fonts.cdnfonts.com/css/gill-sans-extrabolddisplay);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap);
body {
  color: #fff;
  background-color: #4369b2;
}

.card {
  color: #fff;
  background-color: #3d60a3;
  border: none;
  border-radius: 0;
}

.btn {
  background: none;
  border: none;
}

.img {
  opacity: 0.5;
  margin-top: 5%;
  width: 100%;
}

.region {
  font-family: 'Lato', sans-serif;
  font-size: 2.5rem;
  align-items: center;
  justify-content: center;
}

.regionName {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lato', sans-serif;
}

.title {
  background-color: #5888e4;
  font-family: 'Gill Sans ExtraBoldDisplay', sans-serif;
}

.details_page,
.country {
  color: #fff;
  font-family: 'Lato', sans-serif;
}

.details_page {
  width: 100%;
  color: #fff;
}

.inside,
.details_page {
  display: flex;
  align-items: center;
  justify-content: center;
}

.cases {
  background-color: #5888e4;
}

header {
  background-color: #3d60a3;
  color: #fff;
  border-bottom: 1px solid #e8e8e8;
}

nav {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  padding: 1.813rem 0;
}

.nav {
  display: flex;
  justify-content: space-between;
}

.nav-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  color: #0290ff;
  white-space: nowrap;
}

.nav-books,
.nav-categories {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.3rem;
  letter-spacing: 1.9px;
  color: #121212;
  opacity: 0.6;
}

a:focus {
  color: #000;
  opacity: 1;
}

.user-icon-wrapper {
  border: solid 1px #e8e8e8;
  border-radius: 50%;
}

.fa-user-alt {
  padding: 1.2rem;
  color: #0290ff;
}

.nav-bar {
  display: flex;
  align-items: baseline;
  grid-gap: 3rem;
  gap: 3rem;
}

nav ul {
  display: flex;
  grid-gap: 3rem;
  gap: 3rem;
  list-style: none;
}

